import React, {Component} from 'react';
import './Chat.css';
import sendImg from './assets/send@3x.png'
import {BrowserRouter as Router, Route, Link} from "react-router-dom";
import smoothscroll from 'smoothscroll-polyfill';

const text = {
    startChat: '이야기를 해보세요',
    domainUrl: 'www.busk.com/test'
}
const color = ['#49a48c', '#eabf42', '#d78cad', '#3685bf']
const list = ['안녕하세요~~','항상 잘보고 있습니다.','애국가 불러주세요!!!!!!!!','즐거운 시간 보내고 있습니다.','임재범 노래 가능한가요?']
class Chat extends Component {
    constructor(){
        super()
        this.state = {
            roomTitle: '노원역 1번출구 버스킹',
            focused: false,
            chatList: list
        }
        this.setFocused = this.setFocused.bind(this)
        this.releaseFocus = this.releaseFocus.bind(this)
        this.scrollToBottom = this.scrollToBottom.bind(this)
        this.onChatSend = this.onChatSend.bind(this)

        smoothscroll.polyfill();
    }
    onChatSend(e){
        e.preventDefault()
        const value = this.chatInput.value
        if(value.length !== 0){
            this.setState({
                list: this.state.chatList.push(value)
            })
            this.scrollToBottom()
            this.chatInput.value = ''
        }

    }
    setFocused(){
        this.setState({
            focused: true
        })
    }
    releaseFocus(){
        if(this.state.focused){
            this.chatInput.blur()
            this.setState({
                focused: false
            })
        }
    }
    scrollToBottom(){
        this.scrollEnd.scrollIntoView({ behavior: "smooth" });
    }
    render() {
        const {roomTitle, chatList} = this.state
        return (
            <div className={'chat-page'}>
                <div className={'title-wrapper'}>
                    <span>{roomTitle}</span>
                </div>
                <div className={'wrapper'} onClick={this.releaseFocus}>
                    <ul>
                        <li className={'site-url'}>{text.domainUrl}</li>
                        {chatList.map((item, index)=>{
                            return (
                                <li className={'chat'} key={index} style={{background: color[index%4]}}>{item}</li>
                            )
                        })}
                        <li ref={ref=>this.scrollEnd = ref}/>
                    </ul>
                </div>

                <div className={'input-wrapper'}>
                    <form onSubmit={this.onChatSend}>
                        <input type={'text'} placeholder={text.startChat} ref={ref=>this.chatInput = ref} onFocus={this.setFocused}/>
                        <button><img src={sendImg}/></button>
                    </form>
                </div>
            </div>
        )
    }
}

export default Chat;



